import React, { useState, useEffect } from 'react';
import MapVisualization from './components/MapVisualization';
import TimelineChart from './components/TimelineChart';
import StateRankingChart from './components/StateRankingChart';
import NuclearProximityAnalysis from './components/NuclearProximityAnalysis';
import ProximityAnalysis from './components/ProximityAnalysis';
import DataControls from './components/DataControls';
import { processUFOData, processMilitaryBaseData, processNuclearReactorData } from './utils/dataProcessing';
import './styles/App.css';

function App() {
  const [ufoData, setUfoData] = useState([]);
  const [militaryBaseData, setMilitaryBaseData] = useState([]);
  const [nuclearReactorData, setNuclearReactorData] = useState([]);
  const [usMapData, setUsMapData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [loadingProgress, setLoadingProgress] = useState(0);
  
  // Filter states
  const [yearRange, setYearRange] = useState([1950, 2023]); // Default range
  const [selectedStates, setSelectedStates] = useState([]);
  const [selectedShapes, setSelectedShapes] = useState([]);
  const [showMilitaryBases, setShowMilitaryBases] = useState(true);
  const [showNuclearReactors, setShowNuclearReactors] = useState(true);
  const [viewMode, setViewMode] = useState('map'); // 'map', 'heatmap', 'clusters'
  const [activeTab, setActiveTab] = useState('overview'); // 'overview', 'analysis', 'data'
  
  useEffect(() => {
    const fetchData = async () => {
      try {
        console.log("Starting to fetch data...");
        setLoadingProgress(10);
        
        // Load UFO sightings data
        console.log("Fetching UFO data...");
        const ufoResponse = await fetch(`${process.env.PUBLIC_URL}/data/ufo-sightings.csv`);
        if (!ufoResponse.ok) {
          throw new Error(`Failed to load UFO data: ${ufoResponse.status} ${ufoResponse.statusText}`);
        }
        const ufoText = await ufoResponse.text();
        console.log("UFO data sample:", ufoText.substring(0, 200));
        const processedUFOData = processUFOData(ufoText);
        setUfoData(processedUFOData);
        setLoadingProgress(40);
        
        // Load military bases data
        console.log("Fetching military bases data...");
        const basesResponse = await fetch(`${process.env.PUBLIC_URL}/data/military-bases.csv`);
        if (!basesResponse.ok) {
          throw new Error(`Failed to load military bases data: ${basesResponse.status} ${basesResponse.statusText}`);
        }
        const basesText = await basesResponse.text();
        console.log("Military bases data sample:", basesText.substring(0, 200));
        const processedBaseData = processMilitaryBaseData(basesText);
        setMilitaryBaseData(processedBaseData);
        setLoadingProgress(60);
        
        // Load nuclear reactor data
        console.log("Fetching nuclear reactor data...");
        const reactorResponse = await fetch(`${process.env.PUBLIC_URL}/data/nuclear-reactors.csv`);
        if (!reactorResponse.ok) {
          throw new Error(`Failed to load nuclear reactor data: ${reactorResponse.status} ${reactorResponse.statusText}`);
        }
        const reactorText = await reactorResponse.text();
        console.log("Nuclear reactor data sample:", reactorText.substring(0, 200));
        const processedReactorData = processNuclearReactorData(reactorText);
        setNuclearReactorData(processedReactorData);
        setLoadingProgress(80);
        
        // Load US map data
        console.log("Fetching US map data...");
        const mapResponse = await fetch(`${process.env.PUBLIC_URL}/data/us-states.json`);
        if (!mapResponse.ok) {
          throw new Error(`Failed to load US map data: ${mapResponse.status} ${mapResponse.statusText}`);
        }
        const mapData = await mapResponse.json();
        setUsMapData(mapData);
        
        console.log("All data loaded successfully!");
        setLoadingProgress(100);
        setTimeout(() => setLoading(false), 500); // Small delay for smooth transition
      } catch (err) {
        console.error("Error loading data:", err);
        setError('Failed to load data: ' + err.message);
        setLoading(false);
      }
    };
    
    fetchData();
  }, []);
  
  // Extract unique UFO shapes for filtering
  const ufoShapes = [...new Set(ufoData.map(d => d.shape))].filter(shape => shape && shape !== 'unknown');
  
  // Extract all states for filtering
  const allStates = [...new Set(ufoData.map(d => d.state))].filter(state => state);
  
  // Filter data based on selected filters
  const filteredUFOData = ufoData.filter(sighting => {
    const yearMatch = sighting.year >= yearRange[0] && sighting.year <= yearRange[1];
    const stateMatch = selectedStates.length === 0 || selectedStates.includes(sighting.state);
    const shapeMatch = selectedShapes.length === 0 || selectedShapes.includes(sighting.shape);
    return yearMatch && stateMatch && shapeMatch;
  });
  
  if (loading) {
    return (
      <div className="loading-screen">
        <div className="loading-content">
          <h1 className="loading-title">UFO Sightings Explorer</h1>
          <div className="loading-progress-container">
            <div 
              className="loading-progress-bar" 
              style={{ width: `${loadingProgress}%` }}
            ></div>
          </div>
          <p className="loading-text">Loading data... {loadingProgress}%</p>
          <div className="loading-animation">
            <div className="ufo-loading">
              <div className="ufo-body"></div>
              <div className="ufo-beam"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="error">
        <h2>Error Loading Data</h2>
        <p>{error}</p>
        <button onClick={() => window.location.reload()}>Try Again</button>
      </div>
    );
  }
  
  return (
    <div className="app">
      <header>
        <h1>UFO Sightings Explorer</h1>
        <p>Investigating patterns between UFO sightings, military installations, and nuclear facilities across the United States</p>
        
        <nav className="main-nav">
          <ul>
            <li className={activeTab === 'overview' ? 'active' : ''}>
              <button onClick={() => setActiveTab('overview')}>Overview</button>
            </li>
            <li className={activeTab === 'analysis' ? 'active' : ''}>
              <button onClick={() => setActiveTab('analysis')}>Analysis</button>
            </li>
            <li className={activeTab === 'data' ? 'active' : ''}>
              <button onClick={() => setActiveTab('data')}>Data</button>
            </li>
          </ul>
        </nav>
      </header>
      
      <div className="dashboard-stats">
        <div className="stat-card">
          <div className="stat-value">{ufoData.length.toLocaleString()}</div>
          <div className="stat-label">Total Sightings</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{filteredUFOData.length.toLocaleString()}</div>
          <div className="stat-label">Filtered Sightings</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{militaryBaseData.length.toLocaleString()}</div>
          <div className="stat-label">Military Bases</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{nuclearReactorData.length.toLocaleString()}</div>
          <div className="stat-label">Nuclear Facilities</div>
        </div>
      </div>
      
      <DataControls 
        yearRange={yearRange}
        onYearRangeChange={setYearRange}
        selectedStates={selectedStates}
        onStateSelection={setSelectedStates}
        allStates={allStates}
        selectedShapes={selectedShapes}
        onShapeSelection={setSelectedShapes}
        ufoShapes={ufoShapes}
        showMilitaryBases={showMilitaryBases}
        onToggleMilitaryBases={() => setShowMilitaryBases(!showMilitaryBases)}
        showNuclearReactors={showNuclearReactors}
        onToggleNuclearReactors={() => setShowNuclearReactors(!showNuclearReactors)}
        viewMode={viewMode}
        onViewModeChange={setViewMode}
      />
      
      {activeTab === 'overview' && (
        <main>
          <section className="main-visualization">
            <div className="visualization-header">
              <h2>Geographic Distribution</h2>
              <div className="view-mode-toggle">
                <button 
                  className={viewMode === 'map' ? 'active' : ''} 
                  onClick={() => setViewMode('map')}
                >
                  Map View
                </button>
                <button 
                  className={viewMode === 'heatmap' ? 'active' : ''} 
                  onClick={() => setViewMode('heatmap')}
                >
                  Heat Map
                </button>
                <button 
                  className={viewMode === 'clusters' ? 'active' : ''} 
                  onClick={() => setViewMode('clusters')}
                >
                  Clusters
                </button>
              </div>
            </div>
            <MapVisualization 
              ufoData={filteredUFOData}
              militaryBaseData={showMilitaryBases ? militaryBaseData : []}
              nuclearReactorData={showNuclearReactors ? nuclearReactorData : []}
              usMapData={usMapData}
              viewMode={viewMode}
            />
          </section>
          
          <section className="supporting-charts">
            <div className="chart-container">
              <div className="chart timeline">
                <TimelineChart 
                  ufoData={ufoData} 
                  yearRange={yearRange}
                  onYearRangeChange={setYearRange}
                />
              </div>
              <div className="chart-description">
                <h3>UFO Sightings Over Time</h3>
                <p>The timeline reveals a dramatic increase in UFO reports starting in the 1990s, with 
                peak reporting around 2010. Early sightings were rare, with minimal reports before 1950.
                The significant spike in the late 1990s and 2000s may reflect increased public interest
                and improved reporting methods through the internet.</p>
              </div>
            </div>
            
            <div className="chart-container">
              <div className="chart state-ranking">
                <StateRankingChart 
                  ufoData={ufoData}
                  selectedStates={selectedStates}
                  onStateSelection={setSelectedStates}
                />
              </div>
              <div className="chart-description">
                <h3>State-by-State Analysis</h3>
                <p>California leads with over 10,000 reported sightings, more than double any other state.
                Coastal states generally show higher reporting rates, with Washington, Florida, and Texas all in the top five.
                Population centers appear to correlate with higher reporting frequencies.</p>
              </div>
            </div>
            
            <div className="chart-container">
              <div className="chart nuclear-proximity">
                <NuclearProximityAnalysis 
                  ufoData={filteredUFOData}
                  nuclearReactorData={nuclearReactorData}
                />
              </div>
              <div className="chart-description">
                <h3>Nuclear Facility Proximity</h3>
                <p>This analysis shows the percentage of UFO sightings within various distances of nuclear facilities.
                Only 0.3% of sightings occur within 10km of a reactor, while over 90% are within 1000km.
                The data suggests no unusually strong correlation between sighting locations and nuclear sites,
                as most populated areas in the US are within 1000km of at least one nuclear facility.</p>
              </div>
            </div>
          </section>
        </main>
      )}
      
      {activeTab === 'analysis' && (
        <main>
          <section className="analysis-section">
            <h2>Advanced Analysis</h2>
            <div className="analysis-grid">
              <div className="analysis-card">
                <h3>Temporal Patterns</h3>
                <div className="analysis-content">
                  <p>UFO sightings show distinct patterns by time of day, with most reports occurring between 6 PM and 3 AM. 
                  Weekend sightings are approximately 23% higher than weekday sightings, possibly due to more people being outdoors 
                  during leisure time.</p>
                  <div className="chart temporal-analysis">
                    {/* Placeholder for temporal analysis chart */}
                    <div className="placeholder-chart">
                      <span>Temporal Analysis Chart</span>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="analysis-card">
                <h3>Shape Distribution</h3>
                <div className="analysis-content">
                  <p>The most commonly reported UFO shapes are "light" (21%), "triangle" (17%), and "circle" (14%). 
                  Interestingly, triangular sightings increased significantly in the 1990s, possibly correlating with 
                  the development of stealth aircraft technology.</p>
                  <div className="chart shape-analysis">
                    {/* Placeholder for shape analysis chart */}
                    <div className="placeholder-chart">
                      <span>Shape Distribution Chart</span>
                    </div>
                  </div>
                </div>
              </div>
              
              <div className="analysis-card">
                <h3>Military Base Proximity</h3>
                <div className="analysis-content">
                  <p>This analysis examines the relationship between UFO sighting locations and military installations.
                  Approximately 12% of all reported sightings occur within 50km of a major military base, compared to
                  an expected 8% if sightings were randomly distributed.</p>
                  <div className="chart">
                    <ProximityAnalysis 
                      ufoData={filteredUFOData}
                      facilityData={militaryBaseData}
                      facilityType="military"
                    />
                  </div>
                </div>
              </div>
              
              <div className="analysis-card">
                <h3>Population Correlation</h3>
                <div className="analysis-content">
                  <p>When normalized for population density, rural areas show surprisingly high reporting rates.
                  Wyoming, Montana, and Vermont have the highest per-capita sighting rates, suggesting factors
                  beyond population may influence sighting frequencies.</p>
                  <div className="chart population-analysis">
                    {/* Placeholder for population correlation chart */}
                    <div className="placeholder-chart">
                      <span>Population Correlation Chart</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </main>
      )}
      
      {activeTab === 'data' && (
        <main>
          <section className="data-section">
            <h2>Data Explorer</h2>
            <div className="data-table-container">
              <table className="data-table">
                <thead>
                  <tr>
                    <th>Date</th>
                    <th>City</th>
                    <th>State</th>
                    <th>Shape</th>
                    <th>Duration</th>
                    <th>Description</th>
                  </tr>
                </thead>
                <tbody>
                  {filteredUFOData.slice(0, 100).map((sighting, index) => (
                    <tr key={index}>
                      <td>{sighting.date || sighting.datetime || 'Unknown'}</td>
                      <td>{sighting.city || 'Unknown'}</td>
                      <td>{sighting.state || 'Unknown'}</td>
                      <td>{sighting.shape || 'Unknown'}</td>
                      <td>{sighting.duration || 'N/A'}</td>
                      <td className="description-cell">{sighting.description || 'No description available'}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
              {filteredUFOData.length > 100 && (
                <div className="data-pagination">
                  <p>Showing 100 of {filteredUFOData.length.toLocaleString()} records</p>
                  <button className="pagination-button">Load More</button>
                </div>
              )}
            </div>
          </section>
        </main>
      )}
      
      <footer>
        <div className="footer-content">
          <div className="footer-section">
            <h4>About This Project</h4>
            <p>This interactive visualization explores the relationship between UFO sightings and critical infrastructure across the United States.</p>
          </div>
          <div className="footer-section">
            <h4>Data Sources</h4>
            <ul>
              <li>NUFORC UFO Sightings Database</li>
              <li>Department of Defense Military Installations</li>
              <li>US Nuclear Regulatory Commission</li>
            </ul>
          </div>
          <div className="footer-section">
            <h4>Methodology</h4>
            <p>Sighting data has been cleaned and geocoded. Proximity analyses use haversine distance calculations to account for Earth's curvature.</p>
          </div>
        </div>
        <div className="footer-bottom">
          <p>&copy; {new Date().getFullYear()} UFO Sightings Explorer | Last updated: March 2025</p>
        </div>
      </footer>
    </div>
  );
}

export default App;