import React, { useState, useEffect } from 'react';
import '../styles/components/DataControls.css';

const DataControls = ({
  yearRange,
  onYearRangeChange,
  selectedStates,
  onStateSelection,
  allStates,
  selectedShapes,
  onShapeSelection,
  ufoShapes,
  showMilitaryBases,
  onToggleMilitaryBases,
  showNuclearReactors,
  onToggleNuclearReactors,
  viewMode,
  onViewModeChange
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [tempYearRange, setTempYearRange] = useState(yearRange);
  const [stateSearchTerm, setStateSearchTerm] = useState('');
  const [shapeSearchTerm, setShapeSearchTerm] = useState('');
  
  // Update tempYearRange when yearRange prop changes
  useEffect(() => {
    setTempYearRange(yearRange);
  }, [yearRange]);
  
  // Handle year range input change
  const handleYearInputChange = (index, value) => {
    const newRange = [...tempYearRange];
    newRange[index] = parseInt(value, 10) || 0;
    setTempYearRange(newRange);
  };
  
  // Apply year range filter
  const applyYearRange = () => {
    // Ensure start year is not greater than end year
    const validRange = [
      Math.min(tempYearRange[0], tempYearRange[1]),
      Math.max(tempYearRange[0], tempYearRange[1])
    ];
    onYearRangeChange(validRange);
  };
  
  // Handle state selection
  const handleStateToggle = (state) => {
    if (selectedStates.includes(state)) {
      onStateSelection(selectedStates.filter(s => s !== state));
    } else {
      onStateSelection([...selectedStates, state]);
    }
  };
  
  // Handle shape selection
  const handleShapeToggle = (shape) => {
    if (selectedShapes.includes(shape)) {
      onShapeSelection(selectedShapes.filter(s => s !== shape));
    } else {
      onShapeSelection([...selectedShapes, shape]);
    }
  };
  
  // Filter states based on search term
  const filteredStates = allStates
    .filter(state => state.toLowerCase().includes(stateSearchTerm.toLowerCase()))
    .sort();
  
  // Filter shapes based on search term
  const filteredShapes = ufoShapes
    .filter(shape => shape.toLowerCase().includes(shapeSearchTerm.toLowerCase()))
    .sort();
  
  return (
    <div className={`data-controls ${isExpanded ? 'expanded' : ''}`}>
      <div className="controls-header" onClick={() => setIsExpanded(!isExpanded)}>
        <h2>Data Filters</h2>
        <button className="toggle-button">
          {isExpanded ? ' Hide Filters' : ' Show Filters'}
        </button>
      </div>
      
      <div className="controls-content">
        <div className="control-section">
          <h3>Time Period</h3>
          <div className="year-range-control">
            <div className="year-input-group">
              <label>Start Year:</label>
              <input 
                type="number" 
                min="1900" 
                max="2023" 
                value={tempYearRange[0]} 
                onChange={(e) => handleYearInputChange(0, e.target.value)}
              />
            </div>
            <div className="year-input-group">
              <label>End Year:</label>
              <input 
                type="number" 
                min="1900" 
                max="2023" 
                value={tempYearRange[1]} 
                onChange={(e) => handleYearInputChange(1, e.target.value)}
              />
            </div>
            <button 
              className="apply-button"
              onClick={applyYearRange}
            >
              Apply
            </button>
          </div>
          <div className="year-range-slider">
            <input 
              type="range" 
              min="1900" 
              max="2023" 
              value={tempYearRange[0]} 
              onChange={(e) => handleYearInputChange(0, e.target.value)}
            />
            <input 
              type="range" 
              min="1900" 
              max="2023" 
              value={tempYearRange[1]} 
              onChange={(e) => handleYearInputChange(1, e.target.value)}
            />
          </div>
        </div>
        
        <div className="control-section">
          <h3>States</h3>
          <div className="search-input">
            <input 
              type="text" 
              placeholder="Search states..." 
              value={stateSearchTerm} 
              onChange={(e) => setStateSearchTerm(e.target.value)}
            />
            {selectedStates.length > 0 && (
              <button 
                className="clear-button"
                onClick={() => onStateSelection([])}
              >
                Clear All
              </button>
            )}
          </div>
          <div className="selection-list">
            {filteredStates.map(state => (
              <div key={state} className="selection-item">
                <label>
                  <input 
                    type="checkbox" 
                    checked={selectedStates.includes(state)} 
                    onChange={() => handleStateToggle(state)}
                  />
                  {state}
                </label>
              </div>
            ))}
            {filteredStates.length === 0 && (
              <div className="no-results">No states match your search</div>
            )}
          </div>
        </div>
        
        <div className="control-section">
          <h3>UFO Shapes</h3>
          <div className="search-input">
            <input 
              type="text" 
              placeholder="Search shapes..." 
              value={shapeSearchTerm} 
              onChange={(e) => setShapeSearchTerm(e.target.value)}
            />
            {selectedShapes.length > 0 && (
              <button 
                className="clear-button"
                onClick={() => onShapeSelection([])}
              >
                Clear All
              </button>
            )}
          </div>
          <div className="selection-list shapes-list">
            {filteredShapes.map(shape => (
              <div key={shape} className="selection-item">
                <label>
                  <input 
                    type="checkbox" 
                    checked={selectedShapes.includes(shape)} 
                    onChange={() => handleShapeToggle(shape)}
                  />
                  {shape.charAt(0).toUpperCase() + shape.slice(1)}
                </label>
              </div>
            ))}
            {filteredShapes.length === 0 && (
              <div className="no-results">No shapes match your search</div>
            )}
          </div>
        </div>
        
        <div className="control-section facilities-section">
          <h3>Facilities</h3>
          <div className="facility-controls">
            <label className="facility-toggle">
              <input 
                type="checkbox" 
                checked={showMilitaryBases} 
                onChange={onToggleMilitaryBases}
              />
              <span className="toggle-label">Military Bases</span>
              <span className="toggle-icon military"></span>
            </label>
            
            <label className="facility-toggle">
              <input 
                type="checkbox" 
                checked={showNuclearReactors} 
                onChange={onToggleNuclearReactors}
              />
              <span className="toggle-label">Nuclear Facilities</span>
              <span className="toggle-icon nuclear"></span>
            </label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DataControls;